import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import CitySearch from './CitySearch.js';

/**
 * Sidebar component for displaying statistics about reported cases.
 * It fetches and displays the count of positive and negative cases from the backend.
 */
const Sidebar = ({ setCurrentView }) => {
  const navigate = useNavigate(); // Use the navigate hook for routing
  const [positiveCount, setPositiveCount] = useState(0);
  const [negativeCount, setNegativeCount] = useState(0);
  const [selectedFile, setSelectedFile] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  const [loading, setLoading] = useState(false);
  const [prediction, setPrediction] = useState(null);
  const [probability, setProbability] = useState(null);
  const [error, setError] = useState(null);
  const [selectedCity, setSelectedCity] = useState(null);
  const [user, setUser] = useState(false);
  const [username, setUsername] = useState(null);
  const [organization, setOrganization] = useState(null);
  const [usernameInput, setUsernameInput] = useState('');
  const [organizationInput, setOrganizationInput] = useState('');

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handlePredict = async () => {
    const formData = new FormData();

    if (!selectedFile) {
      alert("Please select an image first.");
      return;
    }
    else{
      formData.append('image', selectedFile); // 'image' should match the key used in multer
    }

    if (!user) {
      alert('Please sign in before submitting.');
      return;
    }
    else{
      formData.append('user', username);
      formData.append('org', organization);
    }

    

    // Include the latitude and longitude from your selected location
    if (selectedCity) {
      formData.append('lat', selectedCity.lat);
      formData.append('lng', selectedCity.lng);
      
  } else {
      alert("Please select a city first.");
      return;
  }

    setLoading(true);
    setError(null);

    try {
      const response = await fetch('http://localhost:3000/predict', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error('Error in prediction request');
      }

      const result = await response.json();
      const prediction = result.tagName;
      const probability = result.probability;
      setPrediction(prediction); // Update state with the prediction result
      setProbability(probability)
      console.log("Prediction Result:", result); // Log the prediction result for debugging
    } catch (err) {
      setError(err.message); // Handle errors
      console.error('Prediction Error:', err);
    } finally {
      setLoading(false); // Reset loading state
    }
  };

  const removePreview = () => {
    setImagePreview(null);
    setSelectedFile(null);
  };

  /**
   * Function to fetch the counts of positive and negative cases from the API.
   * It updates the state with the fetched counts.
   */
  const fetchCounts = async () => {
    try {
      // Fetch the count of positive cases
      const positiveResponse = await fetch('http://localhost:3000/api/positive-count');
      const positiveData = await positiveResponse.json(); // Parse the JSON response
      setPositiveCount(positiveData.positiveCount); // Update the state with the positive count
  
      // Fetch the count of negative cases
      const negativeResponse = await fetch('http://localhost:3000/api/negative-count');
      if (!negativeResponse.ok) { // Check if the response is not OK
        const errorText = await negativeResponse.text();
        throw new Error(`Failed to fetch negative cases count: ${errorText}`);
      }
      const negativeData = await negativeResponse.json(); // Parse the JSON response
      setNegativeCount(negativeData.negativeCount); // Update the state with the negative count
    } catch (error) {
      console.error('Error fetching counts:', error);
    }
  };

  const handleCitySearch = async (cityName) => {
    try {      
      if (cityName) {
        setSelectedCity({
          name: cityName.name,
          lat: cityName.lat,
          lng: cityName.lng,
        });

      } else {
        throw new Error("City not found");
      }
    } catch (err) {
      console.error("Error fetching location data:", err);
      setError("Could not find location. Please try again.");
    }
  };

  const handleCitySelected = (city) => {
    handleCitySearch(city); // Fetch location when a city is selected
  };

  const handleSignIn = async () => {
    setUsername(usernameInput);
    setOrganization(organizationInput);
  
    const formData = new FormData();
    formData.append('username', usernameInput);
  
    try {
      // Send a request to check if the username exists
      const response = await fetch('http://localhost:3000/api/check-username', {
        method: 'POST',
        body: formData,
      });
  
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
  
      // Now handle the response data correctly
      if (usernameInput === '' || organizationInput === '') {
        setError('Please fill in your Username/Organization');
      } else {
        // Username is available, proceed to set values
        setUsername(usernameInput);
        setOrganization(organizationInput);
  
        // Clear input fields after successful sign-in
        setUsernameInput('');
        setOrganizationInput('');
        setUser(true);
      }
    } catch (error) {
      console.error('Error checking username:', error);
      setError('An error occurred while checking the username.');
    } finally {
      setLoading(false);
    }
  };
  

  // useEffect hook to fetch counts when the component mounts
  useEffect(() => {
    fetchCounts(); // Call fetchCounts to get the initial counts
  }, []); // Empty dependency array ensures this runs only on mount

  return (
    <div className="sidebar"> {/* Sidebar container */}
    <div style={{paddingBottom: '15px'}} className="sign-in">
        <p>Username: {username}</p>
        {/* Input field for username */}
        <input 
          type="text" 
          value={usernameInput} 
          onChange={(e) => setUsernameInput(e.target.value)}
          placeholder="Enter your username"
          required
        />
        <p>Organization: {organization}</p>
        {/* Input field for organization */}
        <input 
          type="text" 
          value={organizationInput} 
          onChange={(e) => setOrganizationInput(e.target.value)}
          placeholder="Enter your organization"
          required
        />
      </div> 
      <button style={{marginBottom: '45px'}} className="button" onClick={handleSignIn}>Sign In</button>

      <button style={{marginBottom: '-5px'}} className="button" onClick={fetchCounts}>Refresh Statistics</button>
      <div>
        <h3>Number of Reported Positive Cases: {positiveCount}</h3>
        <h3>Number of Reported Negative Cases: {negativeCount}</h3>
      </div>
      {/* CitySearch Component */}
      <CitySearch onCitySelected={handleCitySelected} />

      {/* File input for image selection */}
      <input
        className="file-input"
        type="file"
        accept="image/*"
        onChange={handleFileChange}
      />
      {/* Preview of the selected image */}
      {imagePreview && (
        <div className="image-preview">
          <img src={imagePreview} alt="Selected" style={{ width: 'auto', height: '300px' }} />
        </div>
      )}
      {/* Submit for Analysis Button */}
      <button className="button" onClick={handlePredict} disabled={loading && user}>
        {loading ? 'Loading...' : 'Submit for Analysis'}
      </button>
      {prediction && (
        <div style={{paddingBottom: '15px'}} className="prediction-result">
          <h3>Prediction Result:</h3>
          <p>Result: {prediction}</p> {/* Display prediction result */}
          <p>Probability: {probability}</p>
        </div>
      )}
      <button className="button" onClick={removePreview}>
        Cancel
      </button>
      {/* Button to navigate to the gallery */}
      <button style={{ marginTop: '45px'}} className="button" onClick={() => navigate('/gallery')}>
        View Image Gallery 
      </button>
      <button className="button" onClick={() => navigate('/')}>
        Back to Home
      </button>
      {error && <p className="error-message">{error}</p>} {/* Display error message */}
      
    </div>
  );
};

export default Sidebar;
